<template>
	<view class="container">
		<!-- ËøõÂ∫¶Êù° -->
		<view class="progress-container">
			<view class="progress-bar">
				<view class="progress-fill" :style="{ width: progressPercentage + '%' }"></view>
			</view>
			<view class="progress-text">{{ currentQuestionIndex + 1 }} / {{ totalQuestions }}</view>
		</view>
		
		<!-- ËØÑ‰º∞Á±ªÂûãÊ†áÁ≠æ -->
		<view class="assessment-type">
			<text class="type-label">{{ currentAssessmentType }}</text>
		</view>
		
		<!-- ÈóÆÈ¢òÂå∫Âüü -->
		<view class="question-container" v-if="currentQuestion">
			<view class="question-title">{{ currentQuestion.title }}</view>
			<view class="question-description" v-if="currentQuestion.description">
				{{ currentQuestion.description }}
			</view>
			
			<!-- ÈÄâÈ°πÂå∫Âüü -->
			<view class="options-container">
				<view 
					class="option-item" 
					v-for="(option, index) in currentQuestion.options" 
					:key="index"
					:class="{ active: selectedAnswer === index }"
					@click="selectAnswer(index)"
				>
					<view class="option-icon">{{ option.icon }}</view>
					<view class="option-content">
						<view class="option-text">{{ option.text }}</view>
						<view class="option-score" v-if="option.score !== undefined">
							ÂæóÂàÜ: {{ option.score }}
						</view>
					</view>
				</view>
			</view>
		</view>
		
		<!-- Â∫ïÈÉ®ÊåâÈíÆ -->
		<view class="footer-actions">
			<button 
				class="btn-secondary" 
				@click="previousQuestion" 
				v-if="currentQuestionIndex > 0"
			>
				‰∏ä‰∏ÄÈ¢ò
			</button>
			<button 
				class="btn-primary" 
				@click="nextQuestion"
				:disabled="selectedAnswer === null"
			>
				{{ isLastQuestion ? 'ÂÆåÊàêËØÑ‰º∞' : '‰∏ã‰∏ÄÈ¢ò' }}
			</button>
		</view>
		
		<!-- ÂÆåÊàêËØÑ‰º∞ÂºπÁ™ó -->
		<view class="modal-overlay" v-if="showCompleteModal" @click="closeModal">
			<view class="modal-content" @click.stop>
				<view class="modal-title">ËØÑ‰º∞ÂÆåÊàê</view>
				<view class="modal-message">
					ÊÅ≠ÂñúÔºÅÊÇ®Â∑≤ÂÆåÊàêÊâÄÊúâËØÑ‰º∞È°πÁõÆ„ÄÇ<br/>
					Á≥ªÁªüÊ≠£Âú®ÁîüÊàêËØ¶ÁªÜÁöÑËØÑ‰º∞Êä•Âëä...
				</view>
				<view class="modal-actions">
					<button class="btn-primary" @click="viewResults">Êü•ÁúãÊä•Âëä</button>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				childInfo: {},
				assessmentData: {
					motor: [
						{
							title: "ÊÇ®ÁöÑÂ≠©Â≠êËÉΩÂ§üÁã¨Á´ãË°åËµ∞ÂêóÔºü",
							description: "ÂåÖÊã¨ËÉΩÂ§üÁ®≥ÂÆöÂú∞Ëµ∞Âá†Ê≠•Ë∑Ø",
							options: [
								{ icon: "‚úÖ", text: "ËÉΩÂ§üÁã¨Á´ãË°åËµ∞", score: 3 },
								{ icon: "üîÑ", text: "ÈúÄË¶ÅÊâ∂Áâ©Ë°åËµ∞", score: 2 },
								{ icon: "‚ùå", text: "Ëøò‰∏çËÉΩË°åËµ∞", score: 1 }
							]
						},
						{
							title: "ÊÇ®ÁöÑÂ≠©Â≠êËÉΩÂ§üÁà¨Ê•ºÊ¢ØÂêóÔºü",
							description: "ÂåÖÊã¨Êâ∂ÁùÄÊâ∂ÊâãÊàñÁã¨Á´ãÁà¨Ê•ºÊ¢Ø",
							options: [
								{ icon: "‚úÖ", text: "ËÉΩÂ§üÁã¨Á´ãÁà¨Ê•ºÊ¢Ø", score: 3 },
								{ icon: "üîÑ", text: "ÈúÄË¶ÅÊâ∂Êâ∂Êâã", score: 2 },
								{ icon: "‚ùå", text: "Ëøò‰∏çËÉΩÁà¨Ê•ºÊ¢Ø", score: 1 }
							]
						},
						{
							title: "ÊÇ®ÁöÑÂ≠©Â≠êËÉΩÂ§üÊäïÊé∑ÁêÉÁ±ªÂêóÔºü",
							description: "ÂåÖÊã¨‰ªª‰ΩïÂΩ¢ÂºèÁöÑÊäïÊé∑Âä®‰Ωú",
							options: [
								{ icon: "‚úÖ", text: "ËÉΩÂ§üÂáÜÁ°ÆÊäïÊé∑", score: 3 },
								{ icon: "üîÑ", text: "ËÉΩÂ§üÊäïÊé∑‰ΩÜ‰∏çÂáÜÁ°Æ", score: 2 },
								{ icon: "‚ùå", text: "Ëøò‰∏çËÉΩÊäïÊé∑", score: 1 }
							]
						}
					],
					language: [
						{
							title: "ÊÇ®ÁöÑÂ≠©Â≠êËÉΩÂ§üËØ¥Âá∫ÂÆåÊï¥ÁöÑÂè•Â≠êÂêóÔºü",
							description: "ÂåÖÊã¨3-4‰∏™ËØçÁöÑÁÆÄÂçïÂè•Â≠ê",
							options: [
								{ icon: "‚úÖ", text: "ËÉΩÂ§üËØ¥ÂÆåÊï¥Âè•Â≠ê", score: 3 },
								{ icon: "üîÑ", text: "ËÉΩÂ§üËØ¥Áü≠ËØ≠", score: 2 },
								{ icon: "‚ùå", text: "Âè™ËÉΩËØ¥ÂçïËØç", score: 1 }
							]
						},
						{
							title: "ÊÇ®ÁöÑÂ≠©Â≠êËÉΩÂ§üÁêÜËß£ÁÆÄÂçïÁöÑÊåá‰ª§ÂêóÔºü",
							description: "Â¶Ç'ÊääÁé©ÂÖ∑ÁªôÊàë'„ÄÅ'Âùê‰∏ã'Á≠â",
							options: [
								{ icon: "‚úÖ", text: "ËÉΩÂ§üÁêÜËß£Âπ∂ÊâßË°å", score: 3 },
								{ icon: "üîÑ", text: "ÈÉ®ÂàÜÁêÜËß£", score: 2 },
								{ icon: "‚ùå", text: "‰∏çËÉΩÁêÜËß£", score: 1 }
							]
						}
					],
					cognitive: [
						{
							title: "ÊÇ®ÁöÑÂ≠©Â≠êËÉΩÂ§üËØÜÂà´È¢úËâ≤ÂêóÔºü",
							description: "ËÉΩÂ§üËØ¥Âá∫ÊàñÊåáÂá∫Âü∫Êú¨È¢úËâ≤",
							options: [
								{ icon: "‚úÖ", text: "ËÉΩÂ§üËØÜÂà´Â§öÁßçÈ¢úËâ≤", score: 3 },
								{ icon: "üîÑ", text: "ËÉΩÂ§üËØÜÂà´1-2ÁßçÈ¢úËâ≤", score: 2 },
								{ icon: "‚ùå", text: "Ëøò‰∏çËÉΩËØÜÂà´È¢úËâ≤", score: 1 }
							]
						},
						{
							title: "ÊÇ®ÁöÑÂ≠©Â≠êËÉΩÂ§üÊï∞Êï∞ÂêóÔºü",
							description: "ËÉΩÂ§üÊï∞Âà∞5ÊàñÊõ¥Â§ö",
							options: [
								{ icon: "‚úÖ", text: "ËÉΩÂ§üÊï∞Âà∞10‰ª•‰∏ä", score: 3 },
								{ icon: "üîÑ", text: "ËÉΩÂ§üÊï∞Âà∞5", score: 2 },
								{ icon: "‚ùå", text: "Ëøò‰∏çËÉΩÊï∞Êï∞", score: 1 }
							]
						}
					],
					social: [
						{
							title: "ÊÇ®ÁöÑÂ≠©Â≠êËÉΩÂ§ü‰∏éÂÖ∂‰ªñÂ≠©Â≠ê‰∏ÄËµ∑Áé©ËÄçÂêóÔºü",
							description: "ÂåÖÊã¨‰∫íÂä®ÂíåÂàÜ‰∫´",
							options: [
								{ icon: "‚úÖ", text: "ËÉΩÂ§ü‰∏ªÂä®‰∫íÂä®", score: 3 },
								{ icon: "üîÑ", text: "ËÉΩÂ§üË¢´Âä®ÂèÇ‰∏é", score: 2 },
								{ icon: "‚ùå", text: "Ëøò‰∏çËÉΩ‰∏ÄËµ∑Áé©ËÄç", score: 1 }
							]
						},
						{
							title: "ÊÇ®ÁöÑÂ≠©Â≠êËÉΩÂ§üË°®ËææËá™Â∑±ÁöÑÈúÄÊ±ÇÂêóÔºü",
							description: "ÈÄöËøáËØ≠Ë®ÄÊàñÊâãÂäøË°®Ëææ",
							options: [
								{ icon: "‚úÖ", text: "ËÉΩÂ§üÊ∏ÖÊ•öË°®Ëææ", score: 3 },
								{ icon: "üîÑ", text: "ËÉΩÂ§üÈÉ®ÂàÜË°®Ëææ", score: 2 },
								{ icon: "‚ùå", text: "Ëøò‰∏çËÉΩË°®Ëææ", score: 1 }
							]
						}
					]
				},
				currentQuestionIndex: 0,
				selectedAnswer: null,
				answers: [],
				showCompleteModal: false,
				assessmentTypes: ['motor', 'language', 'cognitive', 'social'],
				typeLabels: {
					motor: 'ËøêÂä®ÂèëËÇ≤ËØÑ‰º∞',
					language: 'ËØ≠Ë®ÄÂèëËÇ≤ËØÑ‰º∞',
					cognitive: 'ËÆ§Áü•ÂèëËÇ≤ËØÑ‰º∞',
					social: 'Á§æ‰∫§ÂèëËÇ≤ËØÑ‰º∞'
				}
			}
		},
		computed: {
			// ÂΩìÂâçËØÑ‰º∞Á±ªÂûã
			currentAssessmentType() {
				const typeIndex = Math.floor(this.currentQuestionIndex / this.getQuestionsPerType());
				return this.typeLabels[this.assessmentTypes[typeIndex]] || '';
			},
			
			// ÂΩìÂâçÈóÆÈ¢ò
			currentQuestion() {
				const typeIndex = Math.floor(this.currentQuestionIndex / this.getQuestionsPerType());
				const questionIndex = this.currentQuestionIndex % this.getQuestionsPerType();
				const type = this.assessmentTypes[typeIndex];
				return this.assessmentData[type] ? this.assessmentData[type][questionIndex] : null;
			},
			
			// ÊÄªÈóÆÈ¢òÊï∞
			totalQuestions() {
				return this.childInfo.assessmentTypes ? 
					this.childInfo.assessmentTypes.length * this.getQuestionsPerType() : 0;
			},
			
			// ËøõÂ∫¶ÁôæÂàÜÊØî
			progressPercentage() {
				return this.totalQuestions > 0 ? 
					((this.currentQuestionIndex + 1) / this.totalQuestions) * 100 : 0;
			},
			
			// ÊòØÂê¶ÊòØÊúÄÂêé‰∏ÄÈ¢ò
			isLastQuestion() {
				return this.currentQuestionIndex >= this.totalQuestions - 1;
			}
		},
		onLoad() {
			// Ëé∑ÂèñÂÑøÁ´•‰ø°ÊÅØ
			this.childInfo = uni.getStorageSync('childInfo') || {};
			
			// Ê†πÊçÆÈÄâÊã©ÁöÑËØÑ‰º∞Á±ªÂûãËøáÊª§ÈóÆÈ¢ò
			this.filterAssessmentData();
		},
		methods: {
			// ËøáÊª§ËØÑ‰º∞Êï∞ÊçÆ
			filterAssessmentData() {
				const selectedTypes = this.childInfo.assessmentTypes || [];
				this.assessmentTypes = selectedTypes;
			},
			
			// Ëé∑ÂèñÊØèÁßçÁ±ªÂûãÁöÑÈóÆÈ¢òÊï∞Èáè
			getQuestionsPerType() {
				return 2; // ÊØèÁßçÁ±ªÂûã2‰∏™ÈóÆÈ¢ò
			},
			
			// ÈÄâÊã©Á≠îÊ°à
			selectAnswer(index) {
				this.selectedAnswer = index;
			},
			
			// ‰∏ã‰∏ÄÈ¢ò
			nextQuestion() {
				if (this.selectedAnswer === null) {
					uni.showToast({
						title: 'ËØ∑ÈÄâÊã©‰∏Ä‰∏™Á≠îÊ°à',
						icon: 'none'
					});
					return;
				}
				
				// ‰øùÂ≠òÂΩìÂâçÁ≠îÊ°à
				this.answers.push({
					questionIndex: this.currentQuestionIndex,
					answer: this.selectedAnswer,
					score: this.currentQuestion.options[this.selectedAnswer].score
				});
				
				if (this.isLastQuestion) {
					// ÂÆåÊàêËØÑ‰º∞
					this.completeAssessment();
				} else {
					// ‰∏ã‰∏ÄÈ¢ò
					this.currentQuestionIndex++;
					this.selectedAnswer = null;
				}
			},
			
			// ‰∏ä‰∏ÄÈ¢ò
			previousQuestion() {
				if (this.currentQuestionIndex > 0) {
					this.currentQuestionIndex--;
					this.selectedAnswer = null;
					
					// ÊÅ¢Â§ç‰πãÂâçÁöÑÁ≠îÊ°à
					const previousAnswer = this.answers.find(a => a.questionIndex === this.currentQuestionIndex);
					if (previousAnswer) {
						this.selectedAnswer = previousAnswer.answer;
					}
				}
			},
			
			// ÂÆåÊàêËØÑ‰º∞
			completeAssessment() {
				// ËÆ°ÁÆóÊÄªÂàÜ
				const totalScore = this.answers.reduce((sum, answer) => sum + answer.score, 0);
				const maxScore = this.totalQuestions * 3;
				const scorePercentage = (totalScore / maxScore) * 100;
				
				// ‰øùÂ≠òËØÑ‰º∞ÁªìÊûú
				const assessmentResult = {
					childInfo: this.childInfo,
					answers: this.answers,
					totalScore: totalScore,
					maxScore: maxScore,
					scorePercentage: scorePercentage,
					assessmentDate: new Date().toISOString(),
					assessmentTypes: this.childInfo.assessmentTypes
				};
				
				uni.setStorageSync('assessmentResult', assessmentResult);
				
				// ‰øùÂ≠òÂà∞ÂéÜÂè≤ËÆ∞ÂΩï
				this.saveToHistory(assessmentResult);
				
				// ÊòæÁ§∫ÂÆåÊàêÂºπÁ™ó
				this.showCompleteModal = true;
			},
			
			// ‰øùÂ≠òÂà∞ÂéÜÂè≤ËÆ∞ÂΩï
			saveToHistory(result) {
				let history = uni.getStorageSync('assessmentHistory') || [];
				history.unshift(result);
				
				// Âè™‰øùÁïôÊúÄËøë10Ê¨°ËÆ∞ÂΩï
				if (history.length > 10) {
					history = history.slice(0, 10);
				}
				
				uni.setStorageSync('assessmentHistory', history);
			},
			
			// ÂÖ≥Èó≠ÂºπÁ™ó
			closeModal() {
				this.showCompleteModal = false;
			},
			
			// Êü•ÁúãÁªìÊûú
			viewResults() {
				this.showCompleteModal = false;
				uni.navigateTo({
					url: '/pages/result/result'
				});
			}
		}
	}
</script>

